@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900);

$ch-family: "メイリオ",Meiryo,"STXihei", HEITI TC, "HEITI TC", "黑體-繁", "微軟正黑體", "黑體", sans-serif;
$content-family: "STXihei", HEITI TC, "HEITI TC", "黑體-繁", "微軟正黑體", "黑體", sans-serif;
$en-family: 'Roboto', sans-serif;
$ench: 'Roboto', "STXihei", HEITI TC, "HEITI TC", "黑體-繁", "微軟正黑體", "黑體", sans-serif;


html,body{
	margin:0;
	height:100%;
	width:100%;
	-webkit-text-size-adjust:100%;   // for mobile fontsize
}

a{
	text-decoration: none;
	color: inherit;
	img{border: 0;}
}

:focus{
	outline: 0;
}

button{
	background-color: transparent;
	border: 0;
}

*{
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}

img{
	display: inline-block;	//remove bottom space
	vertical-align: middle;
	max-width: 100%;
	max-height: 100%;
}

/* override the default IOS styles */
input, textarea{
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

input[readonly]{
	pointer-events: none;
}


