$dark-blue: #336de0;
$light-blue: #00bcea;

// $space: 40px;
$spaceMobile: 20px;
// custom faoundation settings here
$global-margin: 20px;
$global-padding: 20px;
$prototype-spacing-breakpoints: true;
$prototype-spacers-count: 8;
$breakpoint-classes: (small medium large xlarge xxlarge);
$responsive-embed-margin-bottom: 0;
$responsive-embed-ratios: (
	default: 16 by 9,
	vertical: 9 by 16,
	panorama: 256 by 81,
	square: 1 by 1,
	custom: 16 by 7,
);
