.m-width{
	max-width: 1124px;
	margin: 0 auto;
}
.contact-banner-outWrap{
	width: 100%;
	height: 100vh;
	position: relative;
	overflow: hidden;
	@include breakpoint(medium down){
		height: 54.67vh;
	};
}
.contact-banner{
	background-position: center center;
	background-size: cover;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	&.vegas-container{height: 100vh !important;}
	&:after{
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: rgba(#1a1a1a, 0.3);
		pointer-events: none;
	}
	.container{
		padding: 176px 126px;
		position: relative;
		z-index: 1;
		@include breakpoint(medium down){
			padding: 160px $spaceMobile 0;
		};
	}
	.title-en{
		font-family: $en-family;
		font-weight: 300;
		font-size: 72px;
		color: #fff;
		margin-bottom: 22px;
		@include breakpoint(medium down){
			font-size: 29px;
			margin-bottom: 12px;
		};
	}
	.title-ch{
		font-family: $content-family;
		font-weight: 700;
		font-size: 28px;
		letter-spacing: 1px;
		color: #fff;
		margin-bottom: 33px;
		@include breakpoint(medium down){
			font-size: 12px;
		};
	}
	.content{
		font-family: $ench;
		font-size: 12px;
		line-height: 2;
		color: #fff;
		max-width: 407px;
		@include breakpoint(medium down){
			display: none;
		};
	}
}
.banner-dots{
	z-index: 4;
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	bottom: 40px;
	@include breakpoint(medium down){
		bottom: $spaceMobile;
	};
	a{
		width: 8px;
		height: 8px;
		background-color: $light-blue;
		margin: 0 10px;
		display: block;
		transform: rotate(45deg);
		transition: all 0.5s;
