@keyframes info-deco{
	0%{
		opacity: 1;
	}
	20%{
		opacity: 0.2;
	}
	50%{
		opacity: 0.7;
	}
	70%{
		opacity: 0.4;
	}
	100%{
		opacity: 0;
	}
}
@keyframes info-deco-reverse{
	0%{
		opacity: 0;
	}
	20%{
		opacity: 0.8;
	}
	50%{
		opacity: 0.3;
	}
	70%{
		opacity: 0.6;
	}
	100%{
		opacity: 1;
	}
}
