.sock-boarding{
	margin: 0 auto 182px;
	max-width: 1282px;
	height: 804px;
	position: relative;
	#boarding-svg{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		.boarding-conveyor{
			.is-transforming{
				animation: conveyor-boarding 10s infinite linear;
			}
		}
		.is-rotate{
			transform-origin: center center;
			animation: screwRotate 2s infinite linear;
		}
		.is-jumping-one{
			transform-origin: 850px 256px;
			animation: jumping-one 5s infinite alternate cubic-bezier(0.18, 0.89, 0.51, 1.67);
		}
		.is-jumping-two{
			transform-origin: 878px 324px;
			animation: jumping-two 2s infinite alternate cubic-bezier(0.18, 0.89, 0.51, 1.67);
		}
		.is-jumping-three{
			transform-origin: 1048px 344px;
			animation: jumping-three 5s 1s infinite alternate cubic-bezier(0.18, 0.89, 0.51, 1.67);
		}
		.is-jumping-four{
			transform-origin: 1030px 444px;
			animation: jumping-four 1s infinite alternate cubic-bezier(0.18, 0.89, 0.51, 1.67);
		}
		.is-jumping-five{
			transform-origin: 1053px 411px;
			animation: jumping-five 1s infinite alternate cubic-bezier(0.18, 0.89, 0.51, 1.67);
		}
		.is-jumping-six{
			transform-origin: 1076px 461px;
			animation: jumping-six 1s infinite alternate cubic-bezier(0.18, 0.89, 0.51, 1.67);
		}
	}
	.boarding-wearing-sock{
		position: absolute;
		top: 573px;
		left: 0;
		width: 52.4%;
		height: 158px;
		overflow: hidden;
		img{
			position: absolute;
			top: 0;
			left: -337px;
			&:nth-child(2){display: none;}
			&:nth-child(3){display: none;}
		}
	}
}
@keyframes conveyor-boarding{
	0%{
		transform: translateY(0);
	}
	100%{
		transform: translateY(331px);
	}
}
@keyframes jumping-one{
	0%{
		transform: rotate(0);
	}
	100%{
		transform: rotate(240deg);
	}
}
@keyframes jumping-two{
	0%{
		transform: rotate(0);
	}
	100%{
		transform: rotate(130deg);
	}
}
@keyframes jumping-three{
	0%{
		transform: rotate(0);
	}
	100%{
		transform: rotate(320deg);
	}
}
@keyframes jumping-four{
	0%{
		transform: rotate(0);
	}
	100%{
		transform: rotate(30deg);
	}
}
@keyframes jumping-five{
	0%{
		transform: rotate(0);
	}
	100%{
		transform: rotate(-20deg);
	}
}
@keyframes jumping-six{
	0%{
		transform: rotate(0);
	}
	100%{
		transform: rotate(90deg);
	}
}
